.payment-cancel-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 200px); /* Account for header and footer */
  background-color: #f8f9fa;
  padding: 2rem;
}

.payment-cancel-content {
  text-align: center;
  padding: 3rem;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  width: 100%;
}

.cancel-icon {
  margin-bottom: 2rem;
}

.cancel-icon i {
  font-size: 5rem;
  color: #dc3545;
}

h1 {
  color: #343a40;
  margin-bottom: 1.5rem;
  font-size: 2.5rem;
}

p {
  color: #6c757d;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.redirect-text {
  color: #888;
  font-style: italic;
  margin-top: 2rem;
}

.redirect-button {
  margin-top: 1.5rem;
  padding: 0.75rem 1.5rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.2s;
}

.redirect-button:hover {
  background-color: #0056b3;
} 