.payment-success-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 200px);
  background-color: #f8f9fa;
  padding: 2rem;
}

.payment-success-content {
  text-align: center;
  padding: 3rem;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  width: 100%;
}

.success-icon {
  font-size: 5rem;
  color: #28a745;
  margin-bottom: 1.5rem;
}

.error-icon {
  font-size: 5rem;
  color: #dc3545;
  margin-bottom: 1.5rem;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 5px solid #f3f3f3;
  border-top: 5px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 1.5rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

h2 {
  color: #343a40;
  margin-bottom: 1rem;
  font-size: 2rem;
}

p {
  color: #6c757d;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.redirect-text {
  color: #888;
  font-style: italic;
  margin-top: 1.5rem;
}

.view-booking-button, .return-button {
  margin-top: 1.5rem;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s;
}

.view-booking-button {
  background-color: #28a745;
  color: white;
}

.view-booking-button:hover {
  background-color: #218838;
}

.return-button {
  background-color: #6c757d;
  color: white;
}

.return-button:hover {
  background-color: #5a6268;
}

.verifying-payment {
  padding: 2rem 0;
} 