.settings-form {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.section {
  margin-bottom: 30px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.section h3 {
  margin-bottom: 20px;
  color: #333;
}

.phone-number-group {
  padding: 15px;
  margin-bottom: 15px;
  border: 1px dashed #ccc;
  border-radius: 5px;
}

.save {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.save:hover {
  background-color: #0056b3;
} 